   position: relative;
   border: 2px dashed transparent;
   text-align: center;
   justify-content: center;
   width: 450px !important;
   font-family: "Jomhuria", sans-serif;
   font-style: normal;
   font-weight: 200;
   font-size: 16px;
   color: #000000;
}

org-chart .node .spacer {
   border: none;
   width: 100%;
   height: 15px;
}

org-chart .node .title {
   position: relative;
   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: center;
   background: #ef3340;
   border-radius: 15px;
   width: 100%;
   font-weight: 400;
   font-size: 32px;
   color: #ffffff;
   z-index: 1;
}

org-chart .node .content {
   position: relative;
   top: -15px;
   width: 100%;
   min-height: 325px;
   border-radius: 0 0 15px 15px;
   display: flex;
   flex-direction: column;
   margin: 0 auto;
   background: #dddddd;
   box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

org-chart tr.lines .topLine {
   border-top: 2px solid black;
}

org-chart tr.lines .rightLine {
   border-right: 1px solid black;
   float: none;
   border-radius: 0;
}

org-chart tr.lines .leftLine {
   border-left: 1px solid black;
   float: none;
   border-radius: 0;
}

org-chart tr.lines .downLine {
   background-color: black;
   margin: 0 auto;
   height: 20px;
   width: 2px;
   float: none;
}

.team-group-section {
   width: 100%;
   padding: 8px;
}

.team-group-title {
   width: 100%;
   font-size: 32px;
   color: gray;
}

.team-group-content {
   width: 100%;
   display: flex;
   flex-direction: column;
   gap: 3px;
}

.team-group-record {
   height: 51px;
   padding: 5px;
   width: 100%;
   border-width: 4px;
   border-style: solid;
   border-radius: 12px;
   border-color: #ef3340;
   background: #ffffff;
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
   font-size: 18px;
}

.team-group-record:hover {
   filter: brightness(0.75);
}

.team-group-record-display {
   display: flex;
   font-size: 20px;
   flex-direction: row;
   justify-content: center;
   gap: 10px;
}

.team-group-record-display > .team-group-record-display:first-child {
   justify-content: end; 
}

.team-group-record-display > .team-group-record-display:last-child {
   justify-content: start;
}

.team-group-record-display img {
   border-radius: 50%;
   width: 30px;
   height: 30px;
}

.team-group-record-display svg {
   margin: 0px 3px;
   width: 12px;
   height: 12px;
   padding: 2px 8px;
   background-color: skyblue;
   border-radius: 5px;
   display: inline-block;
}

/* TODO (Guy): Now we are hardcoding for each display. */
.team-group-record-display-hardcode {
   display: flex;
   flex-direction: row;
}
.display-block {
   display: flex;
   flex: 0.6;
   justify-content: center;
}

.display-block.display-block-right {
   height: 33px;
   display: flex;
   flex: 0.20;
   background-color: #036;
   border-radius: 10px;
   color: #fff;
}

.display-block.display-block-right > .team-group-record-display {
   flex-direction: row;
   align-items: center;
   justify-content: center;
   height: 100%;
}

.team-button-section {
   display: flex;
   flex-direction: row;
   padding: 3px;
   gap: 6px;
   justify-content: flex-end;
   margin-right: 5px;
}

.team-button {
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 2px;
   width: 28px;
   height: 10px;
   padding: 2px 4px;
   background: #1a3e72;
   border: none;
   border-radius: 5px;
   cursor: pointer;
   font-size: 8px;
   color: white !important;
}

.team-chart-toolbar {
   display: flex;
   flex-direction: row;
   align-items: flex-start;
   padding: 17px 24px;
   gap: 10px;
   width: 254px;
   background: #ffffff;
   box-shadow: 0px -1px 4px rgba(0, 0, 0, 0.25);
   border-radius: 20px;
}

.filter-button {
   border-radius: 10px;
   background: #fff;
   box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
   color: #2f27ce;
   font-family: Roboto;
   font-size: 20px;
   font-style: normal;
   font-weight: 400;
   line-height: normal;
   padding: 7px 15px;
   position: absolute;
}

.filter-button:hover {
   background: #2f27ce;
   color: #fff;
}

.filter-popup {
   border-radius: 15px;
   background: #fff;
   box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.active-text {
   color: #ffffff;
   font-family: "Jomhuria", sans-serif;
   height: 6.5px;
   font-size: 8px;
   font-weight: 400;
   text-align: center;
}

.is-active {
   background: #4bc90f;
   cursor: default;
}

.is-inactive {
   background: grey;
   cursor: default;
}

.team-form-button {
   padding: 3px 10px;
   text-align: center;
   font-weight: 400;
   font-size: 17px;
   font-family: "Roboto" sans-serif;
   color: white;
   background: #1a3e72;
   border-radius: 10px;
}

.team-form-button:hover {
   background: #0e2341;
}

.team-form-button:disabled {
   background: #AAA;
}

.filter-apply > div > button > span {
   color: #2f27ce !important;
}

@keyframes skeleton {
   0% { background-color: #ddd; }
   50%   { background-color: #eee; }
   100% { background-color: #ddd}
}

/* -- DATA PANEL -- */
.data-panel-button {
   display: flex;
   border-radius: 10px;
   background: #fff;
   box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
   float: right;
   padding: 5px;
   width: 230px;
   margin-right: 25px;
}

.data-panel-button .fa-users {
   display: block;
   color: #2F27CE;
}

.data-panel-close {
   background: #2F27CE;
   color: #FFF;
   border-radius: 5px;
   padding: 4px 5px;
   float: right;
}

.data-panel-close:hover {
   background: #FFF;
   color: #2F27CE;
   border: 1px solid #2F27CE;
}

.data-panel-employee {
   margin-top: 3px;
   text-align: center;
   border-radius: 8px;
   border: 2px solid #868686;
   height: 22px;
   font-family: Jomhuria;
   font-size: 18px;
   line-height: 25px;
}

.data-panel-employee-list .webix_list_item {
   border-bottom: unset;
   background: transparent;
   overflow: auto;
   max-height: 95%;
}

.data-panel-open {
   background: #FFF;
   color: #2F27CE;
   font-family: "Roboto";
   font-size: 17px;
   font-weight: 900;
   border-radius: 10px;
   float: right !important;
   width: 90%;
   padding: 5px;
   margin-left: 10px;
}

.data-panel-open:hover {
   background: #2F27CE;
   color: #FFF !important;
}

.data-panel-popup {
   border-radius: 10px;
   background: #fff;
   box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
   padding: 10px;
}

.data-panel-tabbar {
   border: solid black;
   border-width: 1px 0px 1px 0px !important;
   padding: 5px;
}

.data-panel-tabbar .webix_all_tabs {
   float: left;
   display: flex;
   gap: 10px;
   height: 25px;
}

.data-panel-tabbar .webix_all_tabs .webix_item_tab {
   color: #000;
   border-radius: 7px;
   background: #FFF;
   font-family: "Jomhuria", sans-serif;
   font-size:20px;
   line-height: normal;
   box-shadow: none;
   border: none;
   display: flex;
   width: 54px !important;
   height: 24px;
   justify-content: center;
   align-items: center;
}

.data-panel-tabbar .webix_all_tabs .webix_selected {
   color: #FFF;
   background: #000 !important;
}

.data-panel-tabbar .webix_all_tabs .webix_item_tab:hover {
   color: #000;
   background: rgba(0, 0, 0, 0.20) !important;
   box-shadow: none;
}

.data-panel-tabbar .webix_all_tabs .webix_item_tab:focus {
   color: #FFF;
   background: #222 !important;
   box-shadow: none;
}

.data-panel-tabview .webix_multiview {
   margin-top: 0px !important;
}

.data-panel-tabview .webix_multiview .webix_list {
   height: 530px !important;
}

/* following a comment here: https://docs.webix.com/api__refs__ui.popup.html
 * so that the popup doesn't close. Not sure if we want to do it this way */
.webix_modal {
   width: 0;
   height: 0;
}

org-chart .team-group-record > .team-group-record-edit-icon {
   cursor: pointer;
   display: none;
   padding: 4px;
}

org-chart .team-group-record:hover > .team-group-record-edit-icon {
   display: block;
}
